@import '../../../assets/scss/variables.scss';

.cart-icon {
    display: inline-block;
    
    :global(.icon) {
        position: relative;
    }
    .count {
        background: $red;
        border-radius: 100%;
        color: $white;
        position: absolute;
        bottom: 50%;
        left: 50%;
        width: 15px;
        height: 15px;
        line-height: normal;
        font-size: 13px;
    }
}